// @flow weak

import React, {
  PureComponent
}                     from 'react';
import PropTypes      from 'prop-types';
import AnimatedView   from '../../components/animatedView/AnimatedView';
import { button, Link, BrowserRouter, Route, Switch, Redirect, withRouter } from 'react-router-dom';
import Children       from '../children/Children';

class Tree extends PureComponent {
  constructor(props) {
    super(props);
    //console.log(props.match.params.file);
    this.state = {
			data: []
    };
  }

  componentDidMount() {
    this.getFolders();
  }

  componentWillUnmount() {
  }

  getFolders() {
    var self = this;
    $.ajax({
        url: args.FBC_URL +"/includes/lib/tree.php?subdomain="+ args.subdomain +"&app_api="+ args.app_api +"&token="+ args.token
    })
    .done(function(data) {
			self.setState({data: data});
    });
  }

  handleClick(item,e) {
		var children = jQuery("#parent_"+ item.id).find("div").children("ul");
		if (children.is(":visible")) {
			jQuery("#parent_"+ item.id +">i").removeClass("icon-icn_flight_folder_open3");
			jQuery("#parent_"+ item.id +">i").addClass("icon-icn_flight_folder_01");
      children.css('display','none');
			children.hide('fast');
		} else {
			jQuery("#parent_"+ item.id +">i").removeClass("icon-icn_flight_folder_01");
			jQuery("#parent_"+ item.id +">i").addClass("icon-icn_flight_folder_open3");
      children.css('display','block');
			children.show('fast');
		}
		//e.stopPropagation();
	}

  showFolders(data){
    var self;
    const folders = [];
    data.map((item, index) => {
      var id = "parent_"+item.id;
      if(item.children) {
        var folder =
          <li className="parent_li" id={id} key={index}>
            <i className="icon-icn_flight_folder_01" onClick={this.handleClick.bind(this,item)}></i>
            <a href="javascript:;" onClick={this.handleClick.bind(this,item)}>
              {item.name}
            </a>
            <Children item={item.children} />
          </li>;
      } else {
        var empty = (item.size == 0) ? {color:"#999999"} : {};
        var folder =
          <li id={id} key={index}>
            <Link to={{ pathname: `/images/${item.id}`, state: {albumName: `${item.name}`} }} style={empty}>
              <i className="icon-icn_album_01" style={empty}></i>
              {item.name}
            </Link>
          </li>;
      }
      folders.push(folder);
    });
    return folders;
  }

  render() {
    var folderTree = (this.state.data.length) ? this.showFolders(this.state.data) : <div><li></li><li></li><li></li><li></li><li></li><li></li><li></li></div>;
    var icon = args.FBC_URL +"/assets/canto-icon.png";
    return(
      <div id="fbc-tree" className="collapse">
      <div className="tree well">
				<ul>
					<li>
            <Link to={{ pathname: '/images', state: {albumName: 'Main Library'} }}>
  						<img id="fbc-icon" src={icon} />
  						Main Library
            </Link>
					</li>
          {folderTree}
        </ul>
      </div>
      </div>
    );
  }
}

export default withRouter(Tree);



