// @flow weak

import React, {
  PureComponent
}                     from 'react';
import PropTypes      from 'prop-types';
import AnimatedView   from '../../components/animatedView/AnimatedView';
import { button }       from 'react-router-dom';
import Modal          from 'react-modal';

const modalStyle = {
  overlay: {
    backgroundColor: "rgba(0, 0, 0, 0.8)",
    zIndex: 99
  },
  content: {
    top: "60px",
    maxWidth: "1140px",
    margin: "0 auto"
  }
};

class Item extends PureComponent {
  constructor(props) {
    super(props);
    //console.log(props.match.params.file);
    this.state = {
      modalIsOpen: (props.modalIsOpen) ? props.modalIsOpen : false,
      item: props.item,
      img: null,
      imgClass: 'blur',
      details: null,
      sizes: null
    };

    this.closeModal = this.closeModal.bind(this);
    this.getSizes = this.getSizes.bind(this);
    this.handleChange = this.handleChange.bind(this);

    jQuery("#fbc-react").css("overflow-y","hidden");
    jQuery("#fbc-react").css("height","100vh");
  }

  closeModal() {
    //this.setState({modalIsOpen: false});
    jQuery('#loader').hide();
    this.props.handleModal(false);
    jQuery("#fbc-react").css("overflow-y","scroll");
    jQuery("#fbc-react").css("height","auto");
  }

  fbc_media_send_to_editor(htmlString) {
    /*
     * TO DO: Featured Image option.
     * This will append the image to the Thumbnail and set as Featured Image on save
     */
    /*
    if(htmlString.attachment_id != -1) {
      window.parent.jQuery('#_thumbnail_id').val(htmlString.attachment_id);
      window.parent.jQuery("#postimagediv").find(".inside").find(".hide-if-no-js").find("img").remove();
      window.parent.jQuery("#postimagediv").find(".inside").find(".hide-if-no-js").prepend(htmlString.attachment);
    }
    */
    var win = window.dialogArguments || opener || parent || top;

    win.send_to_editor(htmlString.attachment);
    //document.getElementById("loader").style.display = "none";
    jQuery('#loader').hide();
    this.closeModal();
  }

  uploadImage(event){
    var self = this;
    jQuery('#loader').show();

    var win = window || opener || parent || top;

    event.preventDefault();
    var target = jQuery(event.target);
    var form = jQuery('#library-form');

    var action = args.FBC_URL +"/includes/lib/copy-media.php?type=file&tab=library";

    $.ajax({
        url: action,
        type: form.attr('method'),
        dataType: 'json',
        data: form.serialize() + '&' + encodeURIComponent(target.attr('id')) + '=true&chromeless=1',
      })
      .done(function(results) {
        jQuery('#loader').hide();
        win.top.wp.data.dispatch('core/editor').updateBlockAttributes(wpBlockClientId,
          {
            mediaID: results.attachment_id,
            mediaURL: results.attachment_url,
            mediaAttachment: results.attachment
          }
        );
        win.top.wp.data.dispatch('core/editor').updateBlockAttributes(wpBlockClientId, { isOpen: false } );
        //self.fbc_media_send_to_editor(results);
      });
      /*
      .fail(function(xhr, status, error) {
        console.log("error");
        console.log(xhr.status);
        console.log(xhr.responseText);
      });
      */

  }

  readableFileSize(size) {
    var units = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
    var i = 0;
    if (size >= 1024) {
      while(size >= 1024) {
          size /= 1024;
          ++i;
      }
      return size.toFixed(1) + ' ' + units[i];
    } else {
      return size + ' ' + units[0];
    }
  }

  componentDidMount() {
    jQuery('#loader').show();
    this.getImage();
    this.getDetails();
    this.getSizes();
  }

  componentWillUnmount() {
  }

  getDetails(){
    var self = this;
    $.ajax({
      url: args.FBC_URL +"/includes/lib/detail.php?id="+ this.state.item.id + "&app_api="+ args.app_api +"&scheme=" + this.state.item.scheme + "&subdomain="+ args.subdomain +"&token="+ args.token,
      dataType: 'json',
    })
    .done(function(result) {
      var details = {
          "id": result.id,
          "scheme": result.scheme,
          "title": result.name,
          "alt": result.name,
          "name": result.name,
          "owner": result.owner,
          "ownerName": result.ownerName,
          "dimensions": result.default["Dimensions"],
          "size": self.readableFileSize(result.size),
          "time": result.time.substring(0,4)+"-"+result.time.substring(4,6)+"-"+result.time.substring(6,8),
          "updated": result.default["Date modified"].substring(0,4)+"-"+result.default["Date modified"].substring(4,6)+"-"+result.default["Date modified"].substring(6,8),
          "description": result.description,
          "copyright": result.copyright,
          "terms": result.termsAndConditions,
          "termsAndConditions": result.termsAndConditions
      };
      self.setState({details: details});
    });
  }

  getImage(){
    var self = this


    $.ajax({
        url: args.FBC_URL +"/includes/lib/download.php?id="+ this.state.item.id +"&app_api="+ args.app_api +"&subdomain="+ args.subdomain +"&token="+ args.token +"&quality=LowJPG"
    })
    .done(function(e) {
      var start = e.toLowerCase().search('location: .*[\r\n]');
      var startStr = e.substring(start);
      var stop = startStr.search('[\r\n]');
      var imgFile = startStr.substring( 10 ,stop);

      self.setState({img: imgFile, imgClass: 'blur lazyloaded'});
      jQuery('#loader').hide();
    });
  }

  getSizes(){
    var self = this;
    $.ajax({
        url: args.FBC_URL +"/includes/lib/sizes.php?abspath="+ args.abspath
    })
    .done(function(e) {
      self.setState({sizes: e})
    });
  }

  handleChange(e){
    var self = this;
    this.setState({
      details: {
        ...self.state.details,
        [e.target.name]: e.target.value
      }
    });
  }

  render() {
    const content = (this.state.details) ?
      <form encType="multipart/form-data" method="post" action={args.action} className="" id="library-form" style={{display:"block",margin:"0 0 0 20px",position: "relative"}}>
        <input id="post_id" name="post_id" type="hidden" value={args.postID} />
        <input id="fbc_id" name="fbc_id" type="hidden" value={this.state.item.id} />
        <input id="fbc_scheme" name="fbc_scheme" type="hidden" value={this.state.item.scheme} />
        <input id="abspath" name="abspath" type="hidden" value={args.abspath} />
        <input id="fbc_flight_domain" name="fbc_flight_domain" type="hidden" value={args.subdomain} />
        <input id="fbc_app_token" name="fbc_app_token" type="hidden" value={args.token}/>
        <input id="fbc_app_api" name="fbc_app_api" type="hidden" value={args.app_api}/>

  			<div id="media-items">
  				<div tabIndex="0" data-id="0" className="fbc attachment-details save-ready">
  					<h5>Attachment Details</h5>
  					<div className="attachment-info">
  						<div className="details" style={{lineHeight:"20px"}}>
  							<div className="filename">{this.state.details.name}</div>
                <div className="uploaded">{this.state.details.updated}</div>
                <div className="filesize">{this.state.details.size}</div>
                <div className="dimensions">{(this.state.details.dimensions != null) ? this.state.details.dimensions.replace('*',' x ') : ''}</div>
                <div className="owner">{this.state.details.owner}</div>
  						</div>
  					</div>

  					<label data-setting="title" className="setting">
  						<span className="name">Title</span>
  						<input type="text" id="title" name="title" value={this.state.details.title.replace(/\.[^.$]+$/, '')} onChange={this.handleChange} />
  					</label>

  					<label data-setting="caption" className="setting">
  						<span className="name">Caption</span>
  						<textarea id="caption" name="caption"></textarea>
  					</label>

  					<label data-setting="alt" className="setting">
  						<span className="name">Alt Text</span>
  						<input type="text" id="alt-text" name="alt" value={this.state.details.alt.replace(/\.[^.$]+$/, '')} onChange={this.handleChange} />
  					</label>

  					<label data-setting="description" className="setting">
  						<span className="name">Description</span>
  						<textarea id="description" name="description" value={this.state.details.description} onChange={this.handleChange} />
  					</label>

  					<label data-setting="copyright" className="setting">
  						<span className="name">Copyright</span>
  						<input type="text" id="copyright" name="copyright" value={this.state.details.copyright} onChange={this.handleChange} />
  					</label>

  					<label data-setting="terms" className="setting">
  						<span className="name">Terms Conditions</span>
  						<textarea id="terms" name="terms" value={this.state.details.terms} onChange={this.handleChange} />
  					</label>
  				</div>
  				<div className="attachment-display-settings">
            <h5>Attachment Display Settings</h5>

  					<label className="setting">
  						<span>Alignment</span>
  						<select data-user-setting="align" name="align" data-setting="align" className="alignment">

  							<option value="left">Left</option>
  							<option value="center">Center</option>
  							<option value="right">Right</option>
  							<option selected value="none">None</option>
  						</select>
  					</label>

  					<label className="setting">
  							<span>Link To</span>
  							<select name="link" className="link-to" data-setting="link" data-user-setting="urlbutton">
  								<option selected value="none">None</option>
  								<option value="file">Media File</option>
  								<option value="post">Attachment Page</option>
  							</select>
  						<input type="text" className="link-to-custom hidden" data-setting="linkUrl"/>
  					</label>

            <label className="setting">
  						<span>Size</span>
              <div dangerouslySetInnerHTML={{__html: this.state.sizes}}></div>
            </label>

  				</div>

  			</div>
  			<p className="ml-submit"></p>
        <br/>
        <div className="row">
          <div className="col-sm-3 text-left">
            <button onClick={this.closeModal.bind(this)}>Cancel</button>
          </div>
          <div className="col-sm-8 text-left">
            <button id="insertAttachment" onClick={this.uploadImage.bind(this)}>Insert into Post</button>
          </div>
        </div>

  		</form> :
      <form style={{display:"block",margin:"0 0 0 20px",position: "relative"}}>
        <div id="media-items">
          <div tabIndex="0" data-id="0" className="fbc attachment-details save-ready">
            <h5>Attachment Details</h5>
            <div className="attachment-info">
              <div className="details">
                <div className="filename"></div>
                <div className="uploaded"></div>
                <div className="filesize"></div>
                <div className="dimensions"></div>
                <div className="owner"></div>
              </div>
            </div>
          </div>
        </div>
        <p className="ml-submit"></p>
        <br/>
        <div className="row">
          <div className="col-sm-3 text-left">
            <button onClick={this.closeModal.bind(this)}>Cancel</button>
          </div>
          <div className="col-sm-8 text-left">
            <button>Loading ...</button>
          </div>
        </div>
      </form>;



    return(
      <Modal
        isOpen={this.state.modalIsOpen}
        contentLabel="Attachment"
        style={modalStyle}>

        <div className="col-sm-8">
          <img src={this.state.img} className={`img-responsive center-block ${this.state.imgClass}`} />
        </div>
        <div className="col-sm-4">
          {content}
        </div>

      </Modal>
    );
  }
}

export default Item;



