// @flow weak

import React, {
  PureComponent
}                     from 'react';
import PropTypes      from 'prop-types';
import AnimatedView   from '../../components/animatedView/AnimatedView';
import BackToTop      from '../../components/backToTop/BackToTop';
import { Link, button }       from 'react-router-dom';
import Modal          from 'react-modal';
import Item           from '../item/Item';
import Tree           from '../tree/Tree';


class Images extends PureComponent {
  static propTypes= {
    // react-router 4:
    match:    PropTypes.object.isRequired,
    location: PropTypes.object.isRequired,
    history:  PropTypes.object.isRequired,

    // views:
    currentView:  PropTypes.string.isRequired,
    enterAbout:   PropTypes.func.isRequired,
    leaveAbout:   PropTypes.func.isRequired
  };

  constructor(props) {
    super(props);
    //console.log(props.match.params.album);
    var keyword = (props.match.params.keyword) ? props.match.params.keyword : null;
    jQuery('#fbc-found').text("-");
    jQuery('#fbc-currentCnt').text("-");

    this.state = {
      item: [],
      images: [],
      xhrPool: [],
      start: 0,
      processingCount: 0,
      modalIsOpen: false,
      album: (props.match.params.album) ? props.match.params.album : null,
      keyword: keyword
    };

    this.getItem = this.getItem.bind(this);
    this.handleScroll = this.handleScroll.bind(this);

    if(props.location.state) {
      var name = (props.location.state.albumName) ? props.location.state.albumName : "Main Library";
      jQuery('#fbc-res').text("Album:");
      jQuery('#fbc-album').text(name);
    }
    if(keyword) {
      jQuery('#fbc-res').text("Search:");
      jQuery('#fbc-album').text(keyword);
    }

  }

  componentWillReceiveProps(newProps){
    var self = this;
    jQuery('#fbc-found').text("-");
    jQuery('#fbc-currentCnt').text("-");

    $.each(self.state.xhrPool, function(idx, jqXHR) {
      jqXHR.abort();
    });

    var album = newProps.match.params.album;
    var keyword = newProps.match.params.keyword;

    var name = "Main Library";
    var text = newProps.location.state;
    if(text) {
      var name = (newProps.location.state.albumName) ? newProps.location.state.albumName : "Main Library";
    }
    jQuery('#fbc-res').text("Album:");
    jQuery('#fbc-album').text(name);

    if(keyword) {
      jQuery('#fbc-res').text("Search:");
      jQuery('#fbc-album').text(keyword);
    }


    this.setState({
      item: [],
      images: [],
      xhrPool: [],
      album: album,
      keyword: keyword,
      start: 0,
      modalIsOpen: false,
      processingCount: 0,
    }, () => {
      this.getList();
    });
  }

  componentDidMount() {
    const { enterAbout } = this.props;
    enterAbout();

    this.getList();
    window.addEventListener("scroll", this.handleScroll);
  }

  componentWillUnmount() {
    const { leaveAbout } = this.props;
    leaveAbout();
    window.removeEventListener("scroll", this.handleScroll);
  }

  setResults(album,found){
    jQuery('#fbc-album').text(album);
    jQuery('#fbc-found').text(found);
  }

  getList() {
    jQuery('#loader').show();
    jQuery('#scroll-to-load').hide();
    var self = this;

    var start = (this.state.start > 0) ? this.state.start : 0;
    var album = (this.state.album != null) ? "&album="+this.state.album : "";
    var keyword = (this.state.keyword != null) ? "&keyword="+this.state.keyword : "";
    var uri = args.FBC_URL +"/includes/lib/get.php?subdomain="+ args.subdomain +"&app_api="+ args.app_api +"&token="+ args.token + album + keyword + "&limit=24&start="+start;

    $.ajax({
        url: uri,
        beforeSend: function (jqXHR, settings) {
          self.state.xhrPool.push(jqXHR);
        }
    })
    .done(function(data) {
      jQuery('#fbc-found').text(data.found);
      var cnt = 0;
      self.setState({
        items: data.results,
        found: data.found
      });
      if(data.found > 0) {
        $.each(data.results, function(k,v) {
          self.getImages(v,data.results.length,data.found);
        });
      } else {
        jQuery('#loader').hide();
        jQuery('#fbc-found').text(0);
        jQuery('#fbc-currentCnt').text(0);
      }
    });
  }

  getImages(item,length,found){
    var self = this;

    $.ajax({
      url: args.FBC_URL +"/includes/lib/download.php?id="+ item.id +"&app_api="+ args.app_api +"&subdomain="+ args.subdomain +"&token="+ args.token,
      beforeSend: function (jqXHR, settings) {
        self.state.xhrPool.push(jqXHR);
      }
    })
    .done(function(e) {
      var start = e.toLowerCase().search('location: .*[\r\n]');
      var startStr = e.substring(start);
      var stop = startStr.search('[\r\n]');

      let docImg = args.FBC_URL +'/assets/document-icon.png';
      var imgFile = (item.scheme == 'image') ? startStr.substring( 10 ,stop) : docImg;

      var image = {
          "id": item.id,
          "scheme": item.scheme,
          "name": item.name,
          "owner": item.owner,
          "ownerName": item.ownerName,
          "size": item.size,
          "time": item.time,
          "img": imgFile,
          "description": item.description
      };

      var arr = self.state.images.slice();
			arr.push(image);
			self.setState({images: arr});

      var currentCount = self.state.processingCount + 1;
      jQuery('#fbc-currentCnt').text(currentCount);
      self.setState({processingCount: currentCount});
      if(currentCount == (self.state.start+length) || currentCount == (found-1)) {
        jQuery('#loader').hide();
        if(currentCount != found && currentCount != (found-1))
          jQuery('#scroll-to-load').show();
        self.setState({start: currentCount});
      }

    });
  }

  getItem(item){
    this.setState({
      item: item,
      modalIsOpen: true
    });
  }

  handleModal(e) {
    this.setState({
      modalIsOpen: e
    });
  }

  handleScroll() {
    var self = this;
    const windowHeight = "innerHeight" in window ? window.innerHeight : document.documentElement.offsetHeight;
    const body = document.body;
    const html = document.documentElement;
    const docHeight = Math.max(body.scrollHeight, body.offsetHeight, html.clientHeight,  html.scrollHeight, html.offsetHeight);
    const windowBottom = windowHeight + window.pageYOffset;
    if (windowBottom >= docHeight) {

      if(this.state.images.length > 1 && this.state.processingCount == this.state.start && this.state.processingCount != (this.state.found-1)) {
        this.setState({start: (this.state.start+1)});
        jQuery('#loader').show();
        jQuery('#scroll-to-load').hide();
        this.getList();
        setTimeout(function(){
          self.setState({start: (self.state.start-1)});
        }, 500);
      }
    }
  }

  handleChange(e) {
  }


  render() {
    const modal = (this.state.modalIsOpen) ? <Item item={this.state.item} modalIsOpen={true} handleModal={this.handleModal.bind(this)} /> : '';

    return(
      <AnimatedView>
        {modal}
        <div className="attachments row" id="__attachments-view-fbc">
          {this.state.images.map((item, index) => (
            <div className="col-lg-2 col-md-3 col-sm-4 col-xs-6" style={{opacity:"1",height:"190px",paddingLeft:"0px"}} onClick={this.getItem.bind(this,item)} key={index}>
              <div className="attachment-preview" style={{opacity:'0.9',backgroundImage:"url("+item.img+")", position:"absolute",width:"100%"}}>
                <div className="filename">{item.name}</div>
              </div>
            </div>
          ))}
        </div>
        <div id="scroll-to-load">
          <i className="fa fa-chevron-down"></i> Scroll down to load more
        </div>
        <BackToTop
          minScrollY={40}
          scrollTo={'fbc-react'}
        />
      </AnimatedView>
    );
  }
}

export default Images;



